import React from 'react'
import { makeStyles, Typography, Card, CardContent } from '@material-ui/core'
import { Rating } from '@material-ui/lab'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'grid',
    gridTemplateColumns:
      '[full-start] minmax(50px, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 8rem) [col-end]) [center-end] minmax(50px, 1fr) [full-end]'
  },
  card: {}
}))

const RateABiz = ({ reviews }) => {
  const classes = useStyles()
  const review = reviews[0]
  return (
    <div className={classes.root}>
      <Card>
        <CardContent>
          <Typography variant="subtitle1" color="textSecondary">
            {review.authorFirstName}
          </Typography>
          <Typography variant="h5">{review.title}</Typography>
          <Rating name="read-only" value={review.rating} readOnly />
          <Typography variant="body1">{review.text}</Typography>
        </CardContent>
      </Card>
    </div>
  )
}
